<template>
    <div class="info">
        <!-- 头部返回 -->
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <van-swipe class="info-swipe" :autoplay="3000" indicator-color="white">
            <!-- 轮播 -->
            <van-swipe-item v-for="item in data.gallery" :key="item.id">
                <img :src="item.img_url" alt="">
            </van-swipe-item>
        </van-swipe>
        <!-- 头部信息 -->
        <div class="header">
            <p> <span></span> 30天无忧退货</p>
            <p> <span></span> 48小时快速退款</p>
            <p> <span></span> 满88元免邮费</p>
        </div>
        <!-- 商品信息 -->
        <div class="content">
            <h4>{{info.name}}</h4>
            <p>{{info.goods_brief}}</p>
            <span>￥{{info.retail_price}}</span>
        </div>
        <div class="select">
            请选择规格数量
            <div class="icon">
                <van-icon name="arrow" />
            </div>
        </div>
        <!-- 商品参数信息 -->
        <h2 class="ptitle">商品参数</h2>
        <div class="parameter">
            <div class="parameter-item" v-for="item in attribute" :key="item.id">
                <span class="name">{{item.name}}</span>
                <span class="val">{{item.value}}</span>
            </div>
        </div>
        <!-- 图片详情 -->
        <div class="img" v-html="info.goods_desc"></div>
        <div class="question">
            <p class="title">
                <span></span>
                常见问题
                <span></span>
            </p>
            <div class="question-list">
                <div class="question-item" v-for="item in data.issue" :key="item.id">
                    <h4><span></span>{{item.question}}</h4>
                    <p>{{item.answer}}</p>
                </div>
            </div>
        </div>
        <!-- 推荐列表 -->
        <div class="look">
            <p class="look-title">大家都在看</p>
            <div class="look-list">
                <div @click="goInfo(item.id)" class="look-item" v-for="item in data.productList" :key="item.id">
                    <img :src="item.list_pic_url" alt="">
                    <p>{{item.name}}</p>
                    <span>￥{{item.retail_price}}</span>
                </div>
            </div>
        </div>
        <!-- 底部功能 -->
        <div class="footer">
            <van-goods-action>
                <van-goods-action-icon @click="addCollect" icon="star-o" text="收藏" />
                <van-goods-action-icon @click="goCart(info.id)" icon="cart-o" text="购物车"  />
                <van-goods-action-button type="warning" text="加入购物车" @click="goShop" />
                <van-goods-action-button type="danger" 
                text="立即购买" @click="goPay" />
            </van-goods-action>
        </div>
        <!-- 弹出层 -->
        <div class="mask" v-if="show">
            <div class="mask-info">
                <div class="close" @click="closeMask">
                    X
                </div>
                <img :src="info.list_pic_url" alt="">
                <span>价格￥{{info.retail_price}}</span>
                <p>请选择数量</p>
                <h4>数量</h4>
                <van-stepper v-model="value" />
            </div>
        </div>
    </div>
</template>

<script>
// 引入接口
    import {
        detailaction,
        addCart,
        addcollect
    } from '@/api/category/goods/info/index.js'
        import {
        submitAction
    } from '@/api/cart/index.js';
    import {
        Toast
    } from 'vant';

    export default {
        data() {
            return {
                data: [],
                info: [],
                attribute: [],
                show: false,
                value: '',
            };
        },
        computed: {

        },
        created() {

        },
        mounted() {
            // 获取商品详情
            detailaction({
                id: this.$route.query.id,
                openId: localStorage.getItem('openId'),
            }).then(res => {
                this.data = res.data;
                this.info = res.data.info;
                this.attribute = res.data.attribute;
            })

        },
        methods: {
            // 返回
            onClickLeft() {
                history.back()
            },
            // 商品详情
            goInfo(id) {
                this.$router.push(`/category/goods/info?id=${id}`)
            },
            // 跳转购物车页面
            goCart(id) {
                this.$router.push(`/cart?id=${id}`)
            },
            // 添加购物车事件
            goShop() {
                // 判断弹出层是否打开
                if (this.show) {
                    // 添加购物车接口
                    addCart({
                        goodsId: this.info.id,
                        number: this.value,
                        openId: localStorage.getItem('openId'),
                    }).then(res => {
                        // 添加成功返回提示
                        if (res.data.data == "success") {
                            Toast.success('添加成功');
                        }
                    })
                    this.show = !this.show;
                    return
                }
                // 没打开则先打开弹出
                this.show = !this.show;
            },
            // 点击立即购买事件
            goPay(){ 
                // 判断弹出层
                if(this.show){
                    // 弹出层打开直接跳转订单页面
                    this.$router.push(`/cart/order?allPrise=${this.info.retail_price*this.value}`);
                }
                // 没打开则先打开弹出层
                this.show = !this.show;
            },
            // 关闭弹出层
            closeMask() {
                this.show = !this.show;
            },
            addCollect() {
                // 收藏接口
                addcollect({
                    goodsId: this.info.id,
                    openId: localStorage.getItem('openId')
                }).then(res => {
                    if (res.data.data == "success") {
                        Toast.success('收藏成功');
                    }
                })
            },
        },
    };
</script>

<style lang="scss">
    .img {
        font-size: 0px;

        img {
            width: 100%;
            margin: 0px;
        }
    }


    .van-overlay {
        top: auto;
        bottom: 50px;
    }
</style>
<style scoped lang="scss">
    $myColor:#fff;

    .info-swipe {
        .van-swipe-item {
            img {
                width: 100%;
                height: 375px;
            }
        }
    }

    .ss {
        position: fixed;
        top: 100px;
        left: 20px;
    }

    .header {
        padding: 10px 10px 30px 10px;
        display: flex;
        justify-content: space-between;

        p {
            display: flex;
            justify-content: center;
            align-items: center;

            span {
                display: block;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                border: 1px solid #f00;
                margin-right: 4px;
            }

            color: #666;
        }
    }

    .content {
        width: 100%;
        background-color: $myColor;
        padding: 10px 0px 30px 0px;
        text-align: center;
        border-bottom: 1px solid #eee;

        h4 {
            font-size: 20px;
            font-weight: normal;
            margin-bottom: 20px;
        }

        p {
            color: #999;
            margin-bottom: 20px;
        }

        span {
            margin-top: .13333rem;
            font-size: .46667rem;
            color: #b4282d;
        }
    }

    .select {
        padding: 20px 10px;
        font-size: 14px;
        background-color: $myColor;

        .icon {
            font-size: 20px;
            float: right;
            color: #999;
        }
    }

    .ptitle {
        background-color: $myColor;
        padding: 10px;
        font-weight: normal;
        font-size: 22px;
    }

    .parameter {
        background-color: #fff;
        padding: 20px;

        .parameter-item {
            background: #f4f4f4;
            padding: 10px 0px;
            margin-bottom: 10px;

            .name {
                display: inline-block;
                width: 25%;

            }

            .val {
                display: inline-block;
                width: 75%;
            }
        }


    }

    .question {
        margin-top: 10px;
        padding: 0px 20px;
        background-color: #fff;

        .title {
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: center;

            span {
                display: inline-block;
                width: 40px;
                height: 1px;
                background-color: #ccc;
                margin: 0px 10px;

            }

            margin-bottom: 10px;
        }

        .question-list {
            .question-item {
                h4 {
                    display: flex;
                    align-items: center;
                    font-weight: normal;
                    font-size: 14px;

                    span {
                        display: inline-block;
                        width: 5px;
                        height: 5px;
                        background-color: #f00;
                        border-radius: 50%;
                        margin-right: 5px;
                    }
                }

                p {
                    padding: 10px;
                    color: #999;
                }
            }
        }
    }

    .look {
        background-color: $myColor;
        margin-top: 50px;

        .look-title {
            padding: 20px 0px;
            text-align: center;
        }

        .look-list {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            background-color: #f4f4f4;

            .look-item {
                width: 49%;
                background-color: $myColor;
                margin-bottom: 5px;

                img {
                    display: block;
                    margin: 0px auto;
                    width: 151px;
                    height: 151px;

                }

                p {
                    padding: 15px;
                }

                span {
                    display: block;
                    padding-left: 15px;
                    color: #9c3232;
                    margin-bottom: 10px;

                }
            }
        }
    }

    .mask {
        position: fixed;
        bottom: 50px;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.6);

        .mask-info {
            width: 100%;
            height: 200px;
            background-color: #fff;
            position: absolute;
            bottom: 0px;
            left: 0;
            padding-bottom: 20px;

            .close {
                width: 50px;
                height: 50px;
                text-align: center;
                line-height: 50px;
                float: right;
            }

            span {
                position: absolute;
                top: 40px;
                left: 120px;
                display: block;
                font-size: 14px;
                color: #f00;
                margin-left: 20px;
            }

            p {
                position: absolute;
                top: 80px;
                left: 120px;
                margin: 0px 20px;

            }

            h4 {
                font-weight: normal;
                margin: 10px 20px;
            }

            .van-stepper {
                margin: 0px 20px;
            }

            img {
                width: 100px;
                height: 100px;
                margin: 20px 20px 0px 10px;
            }
        }

    }
</style>