import request from "@/api/request";
// 商品详情接口
function detailaction(data) {
    return request({
        method: 'get',
        url: 'goods/detailaction',
        data
    })
}

function addcollect(data) {
    return request({
        method: 'POST',
        url: 'collect/addcollect',
        data
    })
}
// 添加购物车
function addCart(data) {
    return request({
        method: 'POST',
        url: 'cart/addCart',
        data
    })
}


export {
    detailaction,
    addCart,
    addcollect

}