import request from "@/api/request";
// 获取购物车列表
function cartList(data) {
    return request({
        method: 'get',
        url: '/cart/cartList',
        data
    })
}
// 订单确认界面数据获取
function submitAction(data) {
    return request({
        method: 'POST',
        url: '/order/submitAction',
        data
    })
}
// 删除购物车商品
function deleteAction(data) {
    return request({
        method: 'get',
        url: '/cart/deleteAction',
        data
    })
}

export {
    cartList,
    deleteAction,
    submitAction
}